@use 'inner';


:root {
	@include inner.custom-property;
	@include inner.custom-property-customized;
}

:is(ul, ol):where(:not([class])) {
	@include inner.un-ordered;
}

dl:where(:not([class])) {
	@include inner.definition;
}

ul[data-nc-list] {
	@include inner.customized;
}
